.footer-container {
  /* position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%; */
  display: grid;
  /* min-height: 200px; TODO remove for testing */
  /* background-color: var(--color-primary-light); */
  background-color: #6F8AB7;
  grid-template-columns: 1fr 1fr 1fr;
  /* grid-template-rows: 50px 1fr 1fr; */
  justify-content: space-around;
  border-radius: 3px;
}

.footer-container a {
  text-decoration: none;
  color: inherit;
}

.footer-header {
  margin: 20px;
  color: white;
  font-size: 25px;
}

.footer-col {
  margin: 20px;
  color: white;
  grid-row-start: 2;
}

.footer-col--1 {
  grid-row-start: 2;
}

.footer-col-list ul {
  padding: 0;
  list-style-type: none;
}

.footer-link {
  margin: 10px 0;
}


@media (max-width: 40em) {
  .footer-container {
    
    background-color: #6F8AB7;
    justify-content: space-around;
    border-radius: 3px;
  }
  .footer-header {
    margin: 20px;
    color: white;
    font-size: 11px;
    text-align: center;
    display: block;
  }
  .footer-col {
  display: none;
  }
  .footer-col--1 {
    display: none;
    }
    .footer-col-list ul {
      display: none;
      }
      .footer-link {
        display: none;
        }
}

